export default{
    namespaced: true,
    state:{
        tags:{
            type:['', '', '', ''],
            ingredient:['', '', '', '', '', '', '', ' '],
        },
        selected:[],
    },
    getters:{
        getTags(state){
            return state.tags;
        },
        getSelected(state){
            return state.selected;
        },
        getSelectedNum(state){
            return state.selected.length;
        }
    },
    mutations:{
        setSelected(state, payload){
            //   , 
            for(let index in state.selected){
                if(state.selected[index] === payload){
                    state.selected.splice(index, 1);
                    return;
                }
            }
            //   , 
            state.selected.push(payload);
            return true;
        },
        initSelectTagState( state ){
            state.selected = [];
        }
    },
    actions:{

    }
}


